*,html{padding:0}body{background:#eee;text-align:center}h1{border-bottom:1px solid #ddd;margin:0 0 24px;padding:24px 0}.container,h1{background:#fff}.container{border:1px solid #ccc;border-radius:4px;margin:0 auto 48px;max-width:450px}.map{background:#ddd;height:250px;margin:48px auto;width:300px}.stack{border-top:1px solid #ddd;margin-top:48px;padding:24px;text-align:left}.stack ul{margin:5px 0 0 18px;padding:0}.stack li{line-height:24px}.by{border-top:1px solid #ddd;padding:12px 24px}.by a,.by a:visited{border-bottom:1px dotted #00f;color:#00f;text-decoration:none}.by a:active,.by a:focus,.by a:hover{border-bottom-style:solid}